export const LanguageProvider: React.FC<LanguageProviderProps> = ({ children }) => {
  const [language, setLanguage] = useState<Language>('ru'); // По умолчанию русский

  const t = (key: string): string => {
    return translations[language][key] || key;
  };

  const getFont = (fontType: 'body' | 'heading' | 'bold' = 'body'): string => {
    if (language === 'ru') {
      switch (fontType) {
        case 'heading':
          return 'GohuFont, gastromond, sans-serif';
        case 'bold':
          return 'GohuFont, MillenniumBold, "Times New Roman", Times, serif';
        case 'body':
        default:
          return 'GohuFont, Millennium, "Times New Roman", Times, serif';
      }
    } else {
      switch (fontType) {
        case 'heading':
          return 'gastromond, sans-serif';
        case 'bold':
          return 'MillenniumBold, "Times New Roman", Times, serif';
        case 'body':
        default:
          return 'Millennium, "Times New Roman", Times, serif';
      }
    }
  };

  return (
    <LanguageContext.Provider value={{ language, setLanguage, t, getFont }}>
      {children}
    </LanguageContext.Provider>
  );
};

export const useLanguage = () => {
  const context = useContext(LanguageContext);
  if (context === undefined) {
    throw new Error('useLanguage must be used within a LanguageProvider');
  }
  return context;
};